import React from 'react';
import { Table, Pagination, Input, Tooltip, Modal, Button, message, Select } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { genderData, userRoleData } from '../../assets/js/constant';
import { UserDetail } from './Detail';
import MD5 from 'crypto-js/md5';
import { SelectArea } from '../../component/SelectArea';
import { throttle } from 'throttle-debounce';
import './index.less';

// api
import { userList, userBatchDelete, userAdd, userChange } from '../../api';

const { Option } = Select;

export default class UserPage extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      tableData: [],
      total: 0,
      currentPage: 1,
      pageSize: 30,
      showDetail: false,
      type: 'add',
      detail: null,
      selectedKeys: [],
      // 
      keyword: '',
      project_owner_type: '',
      area_code: +sessionStorage.getItem('areaCode') || 0,
    }
  };

  componentDidMount () {
    this.getAll();
  }

  render () {
    return (
      <section className="admin-page page-view">
        <header className="header">
          <span className="title"></span>
          <span>
            <Button className="add-btn header-btn" type="primary" disabled={!this.state.selectedKeys.length} onClick={throttle(1000, () => {
              this.onDelete(this.state.selectedKeys)
            })}
            >
              <i className="iconfont icon-piliangshanchu1" />
              
            </Button>
            <Button className="add-btn header-btn" type="primary" onClick={throttle(1000, () => {
              this.setState({
                showDetail: true,
                type: 'add',
                detail: null
              })
            })}
            >
              <i className="iconfont icon-add1" />
              
            </Button>
          </span>
        </header>
        <section className="body">
        <ul className="search-box">
            <li>
              <span className="label"></span>
              <Input
                value={this.state.proj_keyword}
                onChange={(e) => {
                  this.setState({
                    proj_keyword: e.target.value
                  })
                }}
                allowClear
                placeholder=""
                onPressEnter={() => {
                  this.getAll()
                }}
                style={{ width: 140 }}
              />
            </li>
            <li>
              <span className="label"></span>
              <Select value={this.state.project_owner_type + ''} style={{ width: 120 }}
                onChange={(val) => {
                  this.setState({
                    project_owner_type: +val
                  })
                }}
              >
                {
                  Object.keys(userRoleData).map((key) => {
                    return <Option value={key + ''} key={key}>{+key === 0 ? '' : userRoleData[key]}</Option>
                  })
                }
              </Select>
            </li>
            <li>
              <span className="label"></span>
              <SelectArea width={320} area_code={this.state.area_code} visible onChange={({code}) => {
                this.setState({ area_code: code})
              }}
              />
            </li>
            <li>
              <Tooltip title="">
                <Button shape="circle" type="primary" style={{marginRight: 10}} onClick={throttle(1000, this.getAll)}>
                  <i className="iconfont icon-sousuo" />
                </Button>
              </Tooltip>
              <Tooltip title="">
                <Button shape="circle" type="primary" onClick={throttle(1000, () => {
                  this.setState(() => ({
                    keyword: '',
                    project_owner_type: '',
                    area_code: ''
                  }), this.getAll)
                })}
                >
                  <i className="iconfont icon-zhongzhi" />
                </Button>
              </Tooltip>
            </li>
          </ul>
          <Table
            dataSource={this.state.tableData}
            rowKey={r => r.id}
            pagination={false}
            rowSelection={{
              onChange: (keys) => {
                this.setState({
                  selectedKeys: keys
                })
              }
            }}
          >
            <Table.Column title="" dataIndex="name" key="name" />
            <Table.Column title="" min-width="120px" dataIndex="mobile" key="mobile" />
            <Table.Column title="" dataIndex="area_name" key="area_name"
              render={(val, record) => (<span>{record.area_code ? val : ''}</span>)}
            />
            {/* <Table.Column title="" min-width="80px" dataIndex="gender" key="gender"
              render={(val, _) => (<span>{genderData[val]}</span>)}
            /> */}
            <Table.Column title="" min-width="100px" dataIndex="project_owner_type" key="project_owner_type"
              render={(val, _) => (<span>{userRoleData[val]}</span>)}
            />
            <Table.Column title="" width="100px" dataIndex="operation" key="operation"
              render={(_, record) => (
                <>
                  <Tooltip title="">
                    <i className="iconfont icon-xiugai" onClick={throttle(1000, () => {
                      this.setState({
                        showDetail: true,
                        type: 'edit',
                        detail: record
                      })
                    })} 
                    />
                  </Tooltip>
                  <Tooltip title="">
                    <i className="iconfont icon-shanchu" onClick={throttle(1000, () => this.onDelete([record.id]))} />
                  </Tooltip>
                </>
              )}
            />
          </Table>
          <Pagination
            defaultCurrent={this.state.currentPage}
            pageSize={this.state.pageSize}
            showTotal={() => ` ${this.state.total} `}
            total={this.state.total}
            locale={{
              items_per_page: '',
            }}
            showSizeChanger
            onShowSizeChange={(currentPage, pageSize) => {
              this.setState({
                currentPage: 1,
                pageSize
              }, () => {
                this.getAll()
              });
            }}
            onChange={(pageNumber) => {
              this.setState({
                currentPage: pageNumber
              }, () => {
                this.getAll()
              });
            }}
          />
        </section>
      
        {/*  */}
        <UserDetail
          visible={this.state.showDetail}
          onCancel={() => {
            this.setState({
              showDetail: false,
              detail: null
            })
          }}
          onSubmit={(values) => this.onSubmit(values)}
          detail={this.state.detail}
        />
      </section>
    );
  }
  
  // 
  getAll = async () => {
    const res = await userList({
      get_count: this.state.pageSize,
      start_index: (this.state.currentPage - 1) * this.state.pageSize,
      keyword: this.state.keyword,
      project_owner_type: this.state.project_owner_type,
      area_code: this.state.area_code
    });
    if (res) {
      this.setState({
        tableData: res.records,
        total: res.total_count
      })
    }
  }

  // 
  onDelete = (id) => {
    Modal.confirm({
      title: '?',
      icon: <ExclamationCircleOutlined />,
      okText: '',
      cancelText: '',
      onOk: async () => {
        const res = await userBatchDelete(id, {})
        if (res) {
          this.getAll();
        }
      },
      onCancel() {
        console.log('Cancel');
      },
    })
  }

  // 
  onSubmit = async (values) => {
    const {confirmPassword, password, ...others} = values;
    if (this.state.type === 'add') {
      others.password = MD5(password).toString();
    }
    const res = this.state.type === 'add' ?
    await userAdd(values) : await userChange(values.id, values);
    if (res) {
      this.setState({
        showDetail: false,
        detail: null
      });
      message.success(`${this.state.type === 'add' ? '' : ''}`);
      this.getAll();
    }
  }
};
