import React from 'react';
import { Table, Pagination, Input, Tooltip, Modal, Button, message } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { projectState } from '../../assets/js/constant';
import { ProjectDetail } from './Detail';
import { SelectArea } from '../../component/SelectArea';
import { throttle } from 'throttle-debounce';
import './index.less';

// api
import { projectList, projectDelete, projectAdd, projectChange, projectExport, projectImport } from '../../api';

export default class ProjectPage extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      tableData: [],
      total: 0,
      currentPage: 1,
      pageSize: 30,
      showDetail: false,
      type: 'add',
      detail: null,
      // 
      proj_keyword: '',
      user_keyword: '',
      area_code: +sessionStorage.getItem('areaCode') || 0,
    }
  };

  fileInput

  componentDidMount () {
    this.getAll();
  }

  render () {
    return (
      <section className="project-page page-view">
        <header className="header">
          <span className="title"></span>
          <span>
            <Button className="import-btn header-btn" type="primary" onClick={throttle(1000, () => this.fileInput.click())}>
              <i className="iconfont icon-daoru" />
              
            </Button>
            <Button className="export-btn header-btn" type="primary" onClick={throttle(1000, this.exportData)}>
              <i className="iconfont icon-export" />
              
            </Button>
            <Button className="add-btn header-btn" type="primary" onClick={throttle(1000, () => {
              this.setState({
                showDetail: true,
                type: 'add',
                detail: null
              })
            })}
            >
              <i className="iconfont icon-add1" />
              
            </Button> 
          </span>
        </header>
        <section className="body">
          <ul className="search-box">
            <li>
              <span className="label"></span>
              <Input
                value={this.state.proj_keyword}
                onChange={(e) => {
                  this.setState({
                    proj_keyword: e.target.value
                  })
                }}
                allowClear
                placeholder=""
                onPressEnter={() => {
                  this.getAll()
                }}
                style={{ width: 120 }}
              />
            </li>
            <li>
              <span className="label"></span>
              <Input
                value={this.state.user_keyword}
                onChange={(e) => {
                  this.setState({
                    user_keyword: e.target.value
                  })
                }}
                allowClear
                placeholder=""
                onPressEnter={() => {
                  this.getAll()
                }}
                style={{ width: 140 }}
              />
            </li>
            <li>
              <span className="label"></span>
              <SelectArea selectAll width={320} area_code={this.state.area_code} visible onChange={({code}) => {
                this.setState({ area_code: code})
              }}
              />
            </li>
            <li>
              <Tooltip title="">
                <Button shape="circle" type="primary" style={{marginRight: 10}} onClick={throttle(1000, this.getAll)}>
                  <i className="iconfont icon-sousuo" />
                </Button>
              </Tooltip>
              <Tooltip title="">
                <Button shape="circle" type="primary" onClick={throttle(1000,() => {
                  this.setState(() => ({
                    proj_keyword: '',
                    user_keyword: '',
                    area_code: '',
                  }), this.getAll)
                })}
                >
                  <i className="iconfont icon-zhongzhi" />
                </Button>
              </Tooltip>
            </li>
          </ul>
          <Table
            dataSource={this.state.tableData}
            rowKey={r => r.project_id}
            pagination={false}
          >
            <Table.Column title="" dataIndex="name" key="name" />
            <Table.Column title="" dataIndex="area_name" key="area_name"
              render={(val, record) => (<span>{record.area_code ? val : ''}</span>)}
            />
            <Table.Column title="" min-width="80px" dataIndex="state" key="state"
              render={(val, _) => (<span>{projectState[val]}</span>)}
            />
            <Table.Column title="()" width={140} dataIndex="manu_detect_period" key="manu_detect_period" />
            <Table.Column title="()" width={120} dataIndex="inspect_period" key="inspect_period" />
            <Table.Column title="" width={60} dataIndex="image_path" key="image_path" render={(val, _) => 
              val ? 
              (<img alt="" src={window.globalData.host + val} style={{width: 60, height: 60}} />) : <></>
              }
            />
            <Table.Column title="" dataIndex="comment" key="comment" />
            <Table.Column title="" width="100px" dataIndex="operation" key="operation"
              render={(_, record) => (
                <>
                  <Tooltip title="">
                    <i className="iconfont icon-xiugai" onClick={throttle(1000, () => {
                      this.setState({
                        showDetail: true,
                        type: 'edit',
                        detail: record
                      })
                    })} 
                    />
                  </Tooltip>
                  <Tooltip title="">
                    <i className="iconfont icon-renyuan" onClick={throttle(1000, () => this.props.history.push(`/project/user/${record.project_id}`))} />
                  </Tooltip>
                  <Tooltip title="">
                    <i className="iconfont icon-shanchu" onClick={throttle(1000, () => this.onDelete(record.project_id))} />
                  </Tooltip>
                </>
              )}
            />
          </Table>
          <Pagination
            defaultCurrent={this.state.currentPage}
            pageSize={this.state.pageSize}
            showTotal={() => ` ${this.state.total} `}
            total={this.state.total}
            locale={{
              items_per_page: '',
            }}
            showSizeChanger
            onShowSizeChange={(currentPage, pageSize) => {
              this.setState({
                currentPage: 1,
                pageSize
              }, () => {
                this.getAll()
              });
            }}
            onChange={(pageNumber) => {
              this.setState({
                currentPage: pageNumber
              }, () => {
                this.getAll()
              });
            }}
          />
        </section>
      
        {/*  */}
        <ProjectDetail onCancel={() => {
          this.setState({
            showDetail: false,
            detail: null
          })
        }}
        onSubmit={(values) => this.onSubmit(values)}
        detail={this.state.detail}
        visible={this.state.showDetail}
        />

        {/* csv */}
        <input
          type="file"
          ref={r => this.fileInput = r}
          style={{display: 'none'}}
          id="upload-file"
          accept={"text/csv"}
          onClick={throttle(1000, (e) => {
            e.target.value = ''
          })}
          onChange={async (e) => {
            const file = e.target.files[0];

            if (file.type !== 'text/csv' && file.type !== 'application/vnd.ms-excel') {
              message.warning(`CSV`);
              return;
            }

            // 
            if (file) {
              const res = await projectImport({
                input_file: file
              });
              if (res && res.complete_count) {
                this.getAll()
              } else {
                message.error(res.err_msgs?.join(''))
              }
            }
          }}
        />
      </section>
    );
  }
  
  // 
  getAll = async () => {
    const res = await projectList({
      get_count: this.state.pageSize,
      start_index: (this.state.currentPage - 1) * this.state.pageSize,
      proj_keyword: this.state.proj_keyword,
      user_keyword: this.state.user_keyword,
      area_code: this.state.area_code,
    });
    if (res) {
      this.setState({
        tableData: res.records,
        total: res.total_count
      })
    }
  }

  // 
  onDelete = (id) => {
    Modal.confirm({
      title: '?',
      icon: <ExclamationCircleOutlined />,
      okText: '',
      cancelText: '',
      onOk: async () => {
        const res = await projectDelete(id, {})
        if (res) {
          this.getAll();
        }
      },
      onCancel() {
        console.log('Cancel');
      },
    })
  }

  // 
  onSubmit = async (values) => {
    const res = this.state.type === 'add' ?
    await projectAdd(values) : await projectChange(values.project_id, values);
    if (res) {
      this.setState({
        showDetail: false,
        detail: null
      });
      message.success(`${this.state.type === 'add' ? '' : ''}`);
      this.getAll();
    }
  }

  // 
  exportData = async () => {
    const res = await projectExport({
      ignoreResCode: true,
      proj_keyword: this.state.proj_keyword,
      user_keyword: this.state.user_keyword,
      area_code: this.state.area_code,
    })

    const content = "\ufeff" + res;
    const blob = new Blob([content], { type: 'text/csv, chartset=UTF-8' });
    const url = URL.createObjectURL(blob);

    const a = document.createElement('a');
    a.href = url;
    a.style.display = 'none';
    a.download = '.csv';
    a.click();
    URL.revokeObjectURL(a.href)
  }
};
