import React from 'react';
import { Table, Pagination, Input, Tooltip, Modal, Button, message } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { genderData, adminRoleData } from '../../assets/js/constant';
import { AdminDetail } from './Detail';
import MD5 from 'crypto-js/md5';
import { throttle } from 'throttle-debounce';
import './index.less';

// api
import { adminList, adminDelete, adminAdd, adminChange } from '../../api';

export default class AdminPage extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      tableData: [],
      keyword: '',
      total: 0,
      currentPage: 1,
      pageSize: 30,
      showDetail: false,
      type: 'add',
      detail: null
    }
  };

  componentDidMount () {
    this.getAll();
  }

  render () {
    return (
      <section className="admin-page page-view">
        <header className="header">
          <span className="title"></span>
          <span>
            <Button className="add-btn header-btn" type="primary" onClick={throttle(1000, () => {
              this.setState({
                showDetail: true,
                type: 'add',
                detail: null
              })
            })}
            >
              <i className="iconfont icon-add1" />
              
            </Button>
          </span>
        </header>
        <section className="body">
          <ul className="search-box">
            <li>
              <span className="label"></span>
              <Input
                value={this.state.keyword}
                onChange={(e) => {
                  this.setState({
                    keyword: e.target.value
                  })
                }}
                allowClear
                placeholder=""
                onPressEnter={() => {
                  this.getAll()
                }}
                style={{ width: 140 }}
              />
            </li>
            <li>
              <Tooltip title="">
                <Button shape="circle" type="primary" style={{marginRight: 10}} onClick={throttle(1000, this.getAll)}>
                  <i className="iconfont icon-sousuo" />
                </Button>
              </Tooltip>
              <Tooltip title="">
                <Button shape="circle" type="primary" onClick={throttle(1000, () => {
                  this.setState(() => ({
                    keyword: ''
                  }), this.getAll)
                })}
                >
                  <i className="iconfont icon-zhongzhi" />
                </Button>
              </Tooltip>
            </li>
          </ul>
          <Table
            dataSource={this.state.tableData}
            rowKey={r => r.admin_id}
            pagination={false}
          >
            <Table.Column title="" dataIndex="admin_name" key="admin_name" />
            <Table.Column title="" min-width="120px" dataIndex="mobile" key="mobile" />
            <Table.Column title="" dataIndex="area_name" key="area_name"
              render={(val, record) => (<span>{record.area_code ? val : ''}</span>)}
            />
            {/* <Table.Column title="" min-width="80px" dataIndex="gender" key="gender"
              render={(val, _) => (<span>{genderData[val]}</span>)}
            /> */}
            <Table.Column title="" min-width="100px" dataIndex="role" key="role"
              render={(val, _) => (<span>{adminRoleData[val]}</span>)}
            />
            <Table.Column title="" width="100px" dataIndex="operation" key="operation"
              render={(_, record) => (
                <>
                  <Tooltip title="">
                    <i className="iconfont icon-xiugai" onClick={throttle(1000, () => {
                      this.setState({
                        showDetail: true,
                        type: 'edit',
                        detail: {
                          ...record,
                          name: record.admin_name
                        }
                      })
                    })} 
                    />
                  </Tooltip>
                  <Tooltip title="">
                    <i className="iconfont icon-shanchu" onClick={throttle(1000, () => this.onDelete(record.admin_id))} />
                  </Tooltip>
                </>
              )}
            />
          </Table>
          <Pagination
            defaultCurrent={this.state.currentPage}
            pageSize={this.state.pageSize}
            showTotal={() => ` ${this.state.total} `}
            total={this.state.total}
            locale={{
              items_per_page: '',
            }}
            showSizeChanger
            onShowSizeChange={(currentPage, pageSize) => {
              this.setState({
                currentPage: 1,
                pageSize
              }, () => {
                this.getAll()
              });
            }}
            onChange={(pageNumber) => {
              this.setState({
                currentPage: pageNumber
              }, () => {
                this.getAll()
              });
            }}
          />
        </section>
      
        {/*  */}
        
        <AdminDetail
          onCancel={() => {
            this.setState({
              showDetail: false,
              detail: null
            })
          }}
          onSubmit={(values) => this.onSubmit(values)}
          detail={this.state.detail}
          visible={this.state.showDetail}
        />
      </section>
    );
  }
  
  // admin
  getAll = async () => {
    const res = await adminList({
      get_count: this.state.pageSize,
      start_index: (this.state.currentPage - 1) * this.state.pageSize,
      keyword: this.state.keyword
    });
    if (res) {
      this.setState({
        tableData: res.records,
        total: res.total_count
      })
    }
  }

  // 
  onDelete = (id) => {
    Modal.confirm({
      title: '?',
      icon: <ExclamationCircleOutlined />,
      okText: '',
      cancelText: '',
      onOk: async () => {
        const res = await adminDelete(id, {})
        if (res) {
          this.getAll();
        }
      },
      onCancel() {
        console.log('Cancel');
      },
    })
  }

  // 
  onSubmit = async (values) => {
    const {confirmPassword, password, ...others} = values;
    if (this.state.type === 'add') {
      others.password = MD5(password).toString();
    }
    const res = this.state.type === 'add' ?
    await adminAdd(others) : await adminChange(others.admin_id, others);
    if (res) {
      this.setState({
        showDetail: false,
        detail: null
      });
      message.success(`${this.state.type === 'add' ? '' : ''}`);
      this.getAll();
    }
  }
};
