import axios from 'axios';
import qs from 'qs';
import { message } from 'antd';
import { storeInstance } from '../store/index';
import { types } from '../store/types';

const baseURL = window.globalData.host + '/backend/';
axios.interceptors.request.use(config => {
  // axiosContent-typeapplication/x-www-form-urlencoded, multipart/form-data
  // stringify, admin_idaccess_token
  if (config.headers['Content-Type'] !== 'multipart/form-data') {
    // const accessToken = sessionStorage.getItem('access_token') || ''
    // 
    Object.getOwnPropertyNames(config.data).forEach((key) => {
      if (config.data[key] === null) {
        config.data[key] = ''
      }
    })
    // finalParams = {
    //   access_token: accessToken,
    //   ...config.data
    // }
    config.data = qs.stringify(config.data)
  }
  if (config.headers['Content-Type'] === 'multipart/form-data') {
    // formData
    const formData = new FormData();
    Object.getOwnPropertyNames(config.data).forEach((key) => {
      if (config.data[key]) {
        formData.append(key, config.data[key])
      }
    })
    config.data = formData
  }
  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})

axios.interceptors.response.use((response) => {
  // Do something with response data
  return response.data
}, (error) => {
  // Do something with response error
  return Promise.reject(error)
})

// cookie
axios.defaults.withCredentials = true;

// export default axisoInstance
const axisoInstance = async (url = '', data = {}, method = 'POST', contentType = 'application/x-www-form-urlencoded') => {
  let res;

  try {
    res = await axios({
      baseURL,
      url,
      data,
      method,
      headers: {'Content-Type': contentType}
    })
    if (res) {
      if (res.res === 1004) {
        storeInstance.dispatch({
          type: types.SET_TOKEN_STATUS,
          payload: true,
        })
        return false
      }
      if (res.res !== 0 && !data.ignoreResCode) {
        message.error(res.msg)
        return false
      }
      return res
    }
  } catch (error) {
    message.error('')
    // throw new Error(error)
  }
}

export default axisoInstance;
